import './FriendList.css';
import FriendListItem from './FriendListItem';

export default function FriendList(friends) {

  return (
    <ul class="friend-list">
      {friends.map(item => (
        <FriendListItem
          avatar = {item.avatar}
          name = {item.name}
          isOnline={item.isOnline}
          id = {item.id}
        />
      ))}

    </ul>
  )
}